body {
  height: 100vh;
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  --bg-color: var(--tg-theme-bg-color, #fff);
  font-family: sans-serif;
  background-color: var(--bg-color);
  color: var(--tg-theme-text-color, #222);
  font-size: 14px;
  color-scheme: var(--tg-color-scheme);
  font-family: var();
}
body #root {
  height: 100%;
}
body #root .App {
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
body #root .App .wrapper {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
}
body #root .App .wrapper .task-group {
  width: 100%;
  display: flex;
  height: 20px;
  justify-content: space-between;
}
body #root .App .wrapper .task-group button {
  flex-basis: 33%;
  background-color: var(--tg-theme-secondary-bg-color);
  border: 0;
  outline: 0;
}
body #root .App .wrapper > div:last-child {
  width: 100%;
  height: calc(100% - 75px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
body #root .App .wrapper > div:last-child .task-list {
  box-sizing: border-box;
  padding: 15px;
  overflow-y: scroll;
  height: calc(100% - 60px);
}
body #root .App .wrapper > div:last-child .send-button {
  display: block;
  width: 100%;
  font-size: 14px;
  margin: 15px 0 0 0;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  background-color: var(--tg-theme-button-color, #50a8eb);
  color: var(--tg-theme-button-text-color, #ffffff);
  cursor: pointer;
}
body #root .App .text-field {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
}
body #root .App .text-field-inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 80%;
  height: fit-content;
  border-radius: 10px;
  overflow: hidden;
  background-color: #eaeaea;
}
body #root .App .text-field-inner button {
  display: block;
  width: 100%;
  font-size: 14px;
  margin: 0;
  padding: 12px 20px;
  border: none;
  background-color: var(--tg-theme-button-color, #50a8eb);
  color: var(--tg-theme-button-text-color, #ffffff);
  cursor: pointer;
}
body #root .App .text-field-inner input {
  box-sizing: border-box;
  width: 100%;
  min-height: 30px;
  height: fit-content;
  padding: 2px 5px;
  border: 0;
  outline: 0;
}
body #root .App .text-field-inner input[type=text] {
  min-height: 50px;
  overflow-y: initial;
  text-wrap: initial;
}
body #root .App .text-field-inner input:focus {
  padding: 2px 5px;
  outline: 0;
}
body #root .App .text-field-inner .range-wrapper {
  width: 100%;
  background: linear-gradient(107deg, rgb(0, 217, 0) 0%, rgb(255, 0, 0) 100%);
  box-sizing: border-box;
  padding: 0;
}
body #root .App .text-field-inner .range-wrapper .text-field-range {
  width: 100%;
  height: 20px;
  outline: 0;
  opacity: 0.7;
  margin: 0;
  padding: 0;
}

